<template>

        <div>
                <label class="b-24-black" v-html="t('profile.Login')"></label>
        </div>
        <form class="py-5" @submit.prevent="handleLogin">
                <div class="mb-3 px-3 text-start">
                        <label for="disabledTextInput" class="form-label b-16-gray"
                                v-html="t('profile.usuario')"></label>
                        <input type="text" class="form-control py-3" v-model="username"
                                :placeholder="t('profile.usuario')">
                </div>
                <div class="mb-3 px-3 text-start">
                        <label for="disabledTextInput" class="form-label b-16-gray"
                                v-html="t('profile.contraseña')"></label>
                        <input type="password" v-model="password" class="form-control py-3" value="password"
                                :placeholder="t('profile.contraseña')">
                </div>
                <div>
                        <button type="submit" class="btn-black px-3" v-html="t('profile.Login')"></button>
                </div>
        </form>
</template>

<script setup>
import { useI18n } from 'vue-i18n'
const { t } = useI18n()

import { useRouter } from 'vue-router'
import { ref } from 'vue'


const username = ref('')
const password = ref('')
const errorMessage = ref('')
const router = useRouter()

const handleLogin = async () => {
        try {
                const response = await fetch('http://marcoracm-002-site1.mtempurl.com/api/usuarios/Login', {
                        method: 'POST',
                        headers: {
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                                username: username.value,
                                password: password.value
                        })
                })

                const data = await response.json()
                if (data.status == 1) {
                        localStorage.setItem('token', data.data
                        )

                        router.push('/') // Redirige al home
                } else {
                        console.log('Credenciales incorrectas')
                }
        } catch (error) {
                errorMessage.value = 'Error de conexión con el servidor'
                console.error(error)
        }
}


</script>